header {
  z-index: 2;
  height: 56px;
  width: 100%;
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

header > .socials, header > .pages {
  display: none;
}

.menu {
  width: calc(100vw - 40px);
  height: calc(100vh - 40px);
  background-color: #7e847f;
  border-radius: 30px;
  transition: all .5s;
  position: fixed;
  top: 20px;
  left: 20px;
  overflow: hidden;
}

.title {
  z-index: 3;
  letter-spacing: -2%;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
  transition: all .5s;
}

.title:visited {
  color: #000;
}

header .pages .active {
  color: #000;
  background-color: #fff;
}

.pages {
  width: 96px;
  background-color: #7e847f;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.pages li {
  width: 100%;
}

.pages a {
  text-align: center;
  box-sizing: border-box;
  color: #f6f6f6;
  width: 100%;
  height: 36px;
  letter-spacing: 0;
  background-color: #0000;
  border: #0000;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  padding: 6px 26px;
  font-family: inherit;
  font-size: 16px;
  line-height: 1.5;
  transition: all .5s;
  display: flex;
}

.menu .pages {
  z-index: 3;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menu .pages li {
  z-index: 5;
}

.menu .socials {
  z-index: 3;
  position: fixed;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
}

.socials {
  flex-direction: row;
  gap: 12px;
  display: flex;
}

.socials li {
  box-sizing: border-box;
  width: 41px;
  height: 41px;
  border: 1px solid #f6f6f633;
  border-radius: 10px;
}

.socials li a {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.socials li svg {
  fill: #f6f6f6;
  width: 25px;
  height: 25px;
}

header > button {
  z-index: 3;
  background-color: #0000;
  border: #0000;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

header > button svg {
  width: 100%;
  height: 100%;
}

.burger {
  width: 32px;
  height: 16px;
}

.burger svg {
  fill: #7e847f;
  stroke: #7e847f;
}

.cross {
  width: 20px;
  height: 20px;
  transform: scale(1.2);
}

.cross svg {
  fill: #fff;
  stroke: #fff;
}

@media screen and (min-width: 768px) {
  .menu {
    display: none;
  }

  .title {
    font-size: 28px;
    line-height: 28px;
  }

  .burger {
    display: none;
  }

  .socials li {
    width: 44px;
    height: 44px;
    border: 1px solid #7e847f33;
  }

  .socials li:hover {
    border: 1px solid #34383533;
  }

  .socials li:hover svg {
    fill: #3d4240;
  }

  .socials li svg {
    fill: #7e847f;
    width: 28px;
    height: 28px;
  }

  header {
    height: 116px;
    padding: 0 32px;
  }

  header > .socials {
    margin-left: 0;
    display: flex;
  }

  header > .pages {
    width: 211px;
    height: 52px;
    box-sizing: border-box;
    border-radius: 60px;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 0;
    padding: 0 15px;
    display: flex;
    position: relative;
  }

  header > .pages li {
    z-index: 3;
    position: static;
  }

  header > .pages a {
    padding: 6px 15px;
  }
}

